.logo img {
    /*flex: 1;*/
    max-width: 90px;
    display: flex;
    justify-content: left;
    /*background-color: #343541;*/
}

.sidemenu {
    width: 240px;
    padding: 10px;
    background-color: #daf1e9;
    height: 100%;
}
.sampleQuestions {
    background-color: #ffff;
    border: 1px solid white;
    border-radius: 5px;
    cursor: pointer;
    padding: 10px;
    margin-top: 10px;
    color: #689687;
}

.chat-button {
    border: 1px solid white;
    width: 100px;
    height: 60px;
    border-radius: 5px;
    text-align: center;
    transition: ease 0.25s;
    color: #689687;
    font-size: 1.15em;
}

.chat-button:hover {
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
    font-size: 1.25em;
}