.chatbox {
    flex: 1;
    background-color: #ffff;
    position: relative;
}
.chat-log {
    text-align: left;
    height: 80%;
    overflow-y: scroll;
}

.chat-input-holder {
    padding: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
}

.chat-message {
    margin: 10px;
    color: #689687;
    background-color: #daf1e9;
    border-radius: 5px;
    border: none;
}

.chat-message-center {
    display: flex;
    padding: 12px 24px;
    margin: 20px 10px;
    border-radius: 5px;
    max-width: 680px;
}

.message {
    padding: 0 40px;
}

.chat-input-textarea {
    background-color: #e4f3ee;
    width: 70%;
    padding: 12px;
    border-radius: 5px;
    border: none;
    margin: 12px;
    outline: none;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.25);
    font-size: 1.25em;
    color: #689687;
}

.chat-message.chatbot {
    background-color: #ecf3f1;
}

.avatar img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    padding: 5px;
}

.chat-button {
    border: 1px solid white;
    width: 100px;
    height: 60px;
    border-radius: 5px;
    text-align: center;
    transition: ease 0.25s;
    color: #689687;
    font-size: 1.15em;
}

.chat-button:hover {
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
    font-size: 1.25em;
}
